import React, {useState} from 'react';
import {Modal, Form, Col, Button, Row} from "react-bootstrap";

const ContactData=({show, setShow})=> {
    return (
        <Modal
            show={show}
            onHide={() => setShow(false)}
            dialogClassName="modal-60w"
            backdrop="static"
            keyboard={false}
        >
            <Modal.Header className="border-0">
                <h3 className="mb-0">Contact details</h3>
            </Modal.Header>
            <Modal.Body>
                <Form>

                    <Form.Group controlId="formEmail">
                        <Form.Label>Email</Form.Label>
                        <Form.Control type="email" placeholder="" />
                    </Form.Group>

                    <Form.Group controlId="formLinkedIn">
                        <Form.Label>Linkedin</Form.Label>
                        <Form.Control type="text" placeholder="" />
                    </Form.Group>

                    <Row>
                        <Col xs={5}>
                            <Form.Label>Phone number</Form.Label>
                            <Form.Control type="text" placeholder="" />
                        </Col>
                    </Row>

                    <ul className="list-inline mt-4 float-right">
                        <li className="list-inline-item">
                            <Button variant="link" onClick={()=>setShow(false)}>
                                Cancel
                            </Button>
                        </li>
                        <li className="list-inline-item">
                            <Button variant="outline-secondary px-3">
                                Reset
                            </Button>
                        </li>
                        <li className="list-inline-item">
                            <Button variant="primary px-4" type="submit" onClick={()=>setShow(false)}>
                                Save
                            </Button>
                        </li>
                    </ul>
                </Form>
            </Modal.Body>
        </Modal>
    );
}

export default ContactData;